import { modalController } from "@ionic/vue";
import DrilldownTable from "@/apps/ART/views/reports/BasicReportTemplate.vue"
import { DrillDownParams } from "../apps/HTS/types/ReportTypes"
import table from "@/components/DataViews/tables/ReportDataTable"
import { reactive } from "vue";
import { Patientservice } from "@/services/patient_service";
import router from "@/router";

export const drillPatients = reactive({} as Record<string, Patientservice>)

export function DrillPatients(title: string, patientIds: number[]) {
    drillModal({
        title,
        columns: [
            [
                table.thTxt('National ID'),
                table.thTxt('First name'),
                table.thTxt('Last name'),
                table.thTxt('Gender'),
                table.thTxt('Birthdate'),
                table.thTxt('Action')
            ]
        ],
        data: patientIds,
        generator: async (rows: any) => {
            const row = rows.map(async (id: number) => {
                let patient: Patientservice|null = null
                if (drillPatients[id]) {
                    patient = drillPatients[id]
                } else {
                    patient = new Patientservice((await Patientservice.findByID(id)))
                    drillPatients[id] = patient
                }
                return [
                    table.td(patient!.getNationalID()),
                    table.td(patient!.getGivenName()),
                    table.td(patient!.getFamilyName()),
                    table.td(patient!.getGender()),
                    table.tdDate(`${patient!.getBirthdate()}`),
                    table.tdBtn('Show', () => {
                        router.push({ path: `/patient/dashboard/${id}`})
                        modalController.dismiss({})
                    })
                ]
            })
            return Promise.all(row)
        }
    })
}

export async function drillModal(params={} as DrillDownParams) {
    (await modalController.create({
        component: DrilldownTable,
        cssClass: 'large-modal',
        componentProps: {
            title: params.title,
            columns: params.columns,
            rows: params.data,
            footerColor: 'light',
            rowParser: (rows: any) => params.generator(rows),
            paginated: true,
            showFilters: true,
            rowsPerPage: 20,
            showReportStamp: false,
            onFinish: () => modalController.dismiss()
        }
    })).present()
}